@import "../component.scss";

.footer{
	padding-top:65px;
	background-color: #f2f2f2;
	.inner{
		max-width: 1300px;
		margin: 0 auto;
	}
	&-menu-list{
		width: 1300px;
		margin: 0 auto 65px;
		@include clear;
		.menu-item{
			float: left;
			width: 325px;
			padding-left: 20px;
			box-sizing: border-box;
			strong{
				display: block;
				margin-bottom: 15px;
				font-size: 18px;
				font-weight: 700;
				line-height: 1.5;
			}
			li{
				a{
					color: #0061a0;
					font-size: 15px;
					line-height: 1.8;
				}
			}
		}
	}
	&-site{
		position: relative;
		.family{
			margin-bottom: 40px;
			label{
				display: inline-block;
				width: 122px;
				padding-left: 3px;
				font-weight: 700;
				font-size: 14px;
				box-sizing: border-box;
			}
			.selectbox{
				display: inline-block;
				width: 215px;
			}

		}
		.sns{
			position: absolute;
			bottom: 0;
			right: 0;
		}
	}
	&-info{
		@include clear;
		padding-bottom: 75px;
		border-top: 1px solid #ccc;
		.footer-logo{
			float: left;
			margin-top: 75px;
			margin-right: 68px;
		}
		.footer-etc{
			float: left;
			margin-top: 40px;
			font-size: 14px;
			line-height: 1.7;
			.policy-link{
				@include clear;
				margin-bottom: 13px;
				li{
					float: left;
					margin-right: 25px;
					a{
						color: #0061a0;
						line-height: 2.1;
					}
				}
			}
		}
	}
}